import Vue from "vue";
import VueRouter from "vue-router";

import About from "@/components/About.vue";
import Home from "@/components/Home.vue";
import Section from "@/components/Section.vue";

import BlogEntries from '@/statics/blogs.json';

const blogRoutes = Object.keys(BlogEntries).map(section => {
  const children = BlogEntries[section].map(child => ({
    path: child.id,
    name: child.id,
    component: () => import(`@/pages/${section}/${child.id}.md`)
  }))
  return {
    path: `/${section}`,
    name: section,
    component: () => import('@/components/Blogs.vue'),
    children
  }
})

const routes = [
  {
    id: 0,
    path: "/",
    name: "Home",
    component: Home
  },
  {
    id: 1,
    path: "/about",
    name: "About",
    component: About
  },
  {
  id: 2,
  path: "/2019",
  name: "2019",
  component: Section,
  props: { section: '2019' }
  },
  ...blogRoutes
];

Vue.use(VueRouter)

const router = new VueRouter({mode: 'history', routes, linkActiveClass: 'is-active', "blogRoutes": blogRoutes });



export default router;
