<template>

    <div id="blog-section">
        <b-card-group deck>
            <b-card
            img-top
            style="max-width: 20rem;"
            v-for="route in blogRoutes"
            v-bind:img-src="route.image">
            <b-button variant="outline-primary"><b-link v-bind:to="route.path">
                        {{ route.name }}</b-link></b-button>
            <b-card-text>{{ route.description }}</b-card-text>
            </b-card>
        </b-card-group>

    </div>

</template>

<script>
import Vue from 'vue'
import BootstrapVue from 'bootstrap-vue'
Vue.use(BootstrapVue)

import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap-vue/dist/bootstrap-vue.css'

// Importing the named export
import { BCard, BCardImg } from 'bootstrap-vue/es/components'
Vue.component('b-card', BCard)
Vue.component('b-card-img', BCardImg)

import BlogEntries from '@/statics/blogs.json';

var res = {}

Object.keys(BlogEntries).map(section => {
  const children = BlogEntries[section].map(child => ({
    path: `/${section}/${child.id}`,
    name: child.title,
    image: child.image,
    description: child.description,
    section: `${section}`
  }))
  res[section] = children
})

export default {
  name: "Section",
  props: ['section'],
  data: function() {
  return { blogRoutes: res[this.section] }
  }
}

</script>

